.title {
  font-size: 300%;
  display: flex;
  font-weight: bold;
  justify-content: center;
  padding-top: 25px;
  padding-bottom: 15px;
}

.titleFirstHalf {
  color: #FF9B20;
  -webkit-text-stroke: 1px black;
}

.slogan {
  display: flex;
  justify-content: center;
  color: gray;
  padding-bottom: 15px;
}

.banner {
  padding: 20px;
  background: #FF8C17;
  color: white;
  font-size: 150%;
  display: flex;
  width: 100%;
  height: auto;
}

.card {
  padding: 10px;
  height: 100%;
}

.center {
  display: flex;
  padding: 15px;
}

.searchBar {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 5px;
  justify-content: center;
}

.searchInput {
  width: 250px;
}

.searchBtn {
  width: 55px;   
}

.dropdown {
  width: 160px;
}

.grid {
  margin-left: 40px;
}